@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Nunito', serif;
  background-color: rgb(184, 183, 183);
}

html {
  scroll-behavior: smooth;
}
#title {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 15vh;
  margin: 0;
  font-size: 3em;
  color: white;
  background-color: rgb(16, 29, 100);
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 60vw;
  margin-top: 5vh;
  margin-left: 20vw;
  border-radius: 0.3em;
  background-color: rgb(233, 232, 232);
  padding: 0.5vw;
}
form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: auto;
}
input{
  width: 58vw;
  height: 6vh;
  margin: 1.2vh;
  border: hidden;
  font-size: 1em;
  font-weight: bold;
  border-radius: 0.2em;
  font-family: 'Nunito', serif;
}
textarea {
  max-width: 58vw;
  min-width: 58vw;
  min-height: 6vh;
  margin: 1.2vh;
  border: hidden;
  font-size: 1em;
  font-weight: bold;
  border-radius: 0.2em;
  font-family: 'Nunito', serif;
}
.confirmedInput {
  display: flex;
  align-items: center;
  width: 58vw;
  height: 6vh;
  margin: 1.2vh;
  border: hidden;
  font-size: 1em;
  font-weight: bold;
  border-radius: 0.2em;
  font-family: 'Nunito', serif;
  background-color: white;
  
}
button {
  width: 58vw;
  height: 7vh;
  margin: 1vh;
  border: hidden;
  border-radius: 0.5em;
  cursor: pointer;
  color: white;
  background-color: rgb(16, 29, 100);
  font-family: 'Nunito', serif;
  font-size: 1em;
  font-weight: bold;
}
.borrar {
  background-color: rgb(100, 16, 16);
}
.guardar {
  background-color: rgb(16, 100, 68);
}
.list {
  display:flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.subtitle {
  font-size: 2em;
  margin: 1vh;
  font-weight: bold;
}
#previewMainContainer {
  width: 61vw;
  margin-top: 5vh;
  margin-bottom: 5vh;
  margin-left: 20vw;
  height: 130vh;
  border-radius: 0.3em;
  background-color: rgb(233, 232, 232);
}
.pdf {
  margin-top: 5vh;
  background-color: rgb(23, 146, 99);
}
.ejemplo {
  background-color: rgb(230, 124, 39);
}
#curriculum {
  color: rgb(0, 0, 0);
  font-size: 3em;
  text-decoration: underline;
  text-align: center;
}
@media (max-width: 1150px) {
  body {
    background-color: rgb(233, 232, 232);
  }
  #title {
    width: 102vw;
    padding: 1vw;
  }
  .container {
  width: 100vw;
  margin: 0;
  padding: 1vw;
  border: none;
  border-radius: 0%;
  }
  input {
    width: 97vw;
  }
  button {
    width: 97vw;
  }
  textarea {
    max-width: 97vw;
    min-width: 97vw;
  }
  .confirmedInput {
    width: 97vw;
  }
  #previewMainContainer {
    width: 104vw;
    margin: 0;
    border: none;
    border-radius: 0%;
  }
}
#avatar{
    width: 90%;
    margin-top: 5%;
}
#previewContainer{
    display: grid;
    margin: 0;
    padding: 0;
    height: 100%;
    align-items: start;
    width: 61vw;
    grid-template-columns: 15vw 15vw 15vw 16vw;
    grid-template-rows: auto;
    grid-template-areas: "previewMain previewMain previewMain previewMain"
                         "previewDescription previewDescription previewDescription previewContact"
                         "previewExperience previewExperience previewExperience previewContact"
                         "previewEducation previewEducation previewEducation previewContact"
}
#previewMain {
    grid-area: previewMain;
    color: white;
    background-color: rgb(16, 29, 100);
    height: 100%;
    margin: 0;
}
#previewDescription {
    grid-area: previewDescription;
    height: 100%;
    white-space: pre-line;
    white-space: pre-wrap;
}
#previewName{
    font-size: 2em;
    margin: 2vh;
    font-weight: bold;
}
#previewTitle {
    font-size: 1.5em;
    margin: 2vh;
}
.previewSubtitle {
    font-size: 1em;
    margin: 1vh;
    font-weight: bold;
}
#previewContact {
    grid-area: previewContact;
    border-left: 1px solid;
    height: 100%;
    background-color: lightgrey;
}
#previewEducation {
    grid-area: previewEducation
}
#previewExperience {
    grid-area: previewExperience
}
#flex{ 
    display: flex;
    justify-content: center;
  }
p {
    overflow-wrap: break-word;
}
hr{
    width: 50%;
    margin: 0;
}
.contentBold {
    font-size: 1em;
    margin: 1vh;
    font-weight: bold;
}
.content {
    font-size: 1em;
    margin: 1vh;
}

@media (max-width: 1150px) {
    #previewContainer{
        width: 100%;
        grid-template-columns: 25% 25% 25% 25%;
    }
}
